import React, { useState } from "react"
import styled from "styled-components"
import ReactTooltip from "react-tooltip"
import ProblemSolving from "../images/icons/skill_icon_template.svg"
import Acumen from "../images/icons/acumen.svg"
import Insight from "../images/icons/insight.svg"
import Learning from "../images/icons/learning.svg"
import HTMLIcon from "../images/icons/html.svg"
import JavascriptIcon from "../images/icons/javascript.svg"
import MatlabIcon from "../images/icons/matlab.svg"
import PythonIcon from "../images/icons/python.svg"
import VisualBasicIcon from "../images/icons/visualbasic.svg"

const SkillSection = styled.div`
  display: flex;
  margin-top: 1rem;

  @media (max-width: 562px) {
    & button {
      flex: 1;
      margin-right: 0;
    }
  }
  @media (min-width: 762px) {
    & button {
      min-width: 10rem;
      margin-right: 1rem;
    }
  }
  @media (min-width: 992px) {
    & button {
      min-width: 10rem;
      margin-right: 1rem;
    }
  }
  & button {
    padding: ${props =>
      props.active ? "0.4rem 0.5rem" : "0.4rem 0.4rem 0.4rem 0"};
    border: none;
    text-align: left;
    font-size: 1rem;
    font-weight: bold;
    color: ${props => (props.active ? "#242323" : "#FFF")};
    cursor: pointer;
    background: linear-gradient(to right, transparent 50%, white 50%);
    background-size: 400% 200%;
    background-position: left bottom;
    transition: all 0.5s ease;

    border-bottom: 1px solid #fff;
    box-shadow: ${props => (props.active ? "inset 0 0 10px 1px #36454f" : "0")};
  }

  & button:hover {
    background-color: #fff;
    color: #242323;
    padding-left: 0.5rem;
    box-shadow: inset 0 0 10px 1px #36454f;
    background-position: right bottom;
  }

  &button: active {
    transform: scale(1.05);
  }

  & button:focus {
    outline: none;
  }
  margin-bottom: 1rem;
`

const SkillIcons = styled.div`
  width: 100%;
  display: grid;
  @media (max-width: 562px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media (min-width: 762px) {
    grid-template-columns: repeat(5, 1fr);
  }
  @media (min-width: 992px) {
    grid-template-columns: repeat(5, 1fr);
  }

  align-items: center;
  grid-gap: 1rem;
  transition: transform all 0.2s ease;
  animation-name: ${props => (props.active ? "shiftLeft" : "")};
  animation-duration: 2s;

  @keyframes shiftLeft {
    0% {
      transform: translateX(-10%);
    }
    100% {
      transform: translateX(0);
    }
  }
`

const CodeIcons = styled.div`
  width: 100%;
  display: grid;
  @media (max-width: 562px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media (min-width: 762px) {
    grid-template-columns: repeat(5, 1fr);
  }
  @media (min-width: 992px) {
    grid-template-columns: repeat(5, 1fr);
  }
  align-items: center;
  grid-gap: 1rem;
  animation-name: ${props => (props.active ? "shiftRight" : "")};
  animation-duration: 2s;

  @keyframes shiftRight {
    0% {
      transform: translateX(10%);
    }
    100% {
      transform: translateX(0);
    }
  }
`

const SkillIcon = styled.img`
  width: 100%;
  cursor: pointer;
  transition: all 0.2s ease-in;
  margin-bottom: 0;
  &:hover {
    animation-name: spin_rotate;
    animation-duration: 2s;
    filter: invert(1);
  }

  @keyframes spin_rotate {
    0% {
      transform: rotate(0deg) scale(1);
    }

    50% {
      transform: rotate(360deg) scale(1.1);
    }

    100% {
      transform: rotate(720deg) scale(1);
    }
  }
`

const CodeIcon = styled.img`
  width: 100%;
  cursor: pointer;
  transition: all 0.2s ease-in;
  margin-bottom: 0;
  &:hover {
    animation-name: spin_rotate;
    animation-duration: 2s;
  }

  @keyframes spin_rotate {
    0% {
      transform: rotate(0deg) scale(1);
    }

    50% {
      transform: rotate(360deg) scale(1.1);
    }

    100% {
      transform: rotate(720deg) scale(1);
    }
  }
`

const Skill = () => {
  const [skillActive, setSkill] = useState(true)
  const [softwareActive, setSoftware] = useState(false)

  return (
    <>
      <SkillSection>
        <button
          onClick={() => {
            setSkill(true)
            setSoftware(false)
          }}
        >
          TRAITS
        </button>
        <button
          onClick={() => {
            setSkill(false)
            setSoftware(true)
          }}
        >
          CODE
        </button>
      </SkillSection>
      {skillActive ? (
        <SkillIcons active={skillActive}>
          <SkillIcon data-tip data-for="problemSolving" src={ProblemSolving} />
          <ReactTooltip id="problemSolving">
            <span>Innovative Problem Solving with Technology</span>
          </ReactTooltip>
          <SkillIcon data-tip data-for="balance" src={Acumen} />
          <ReactTooltip id="balance">
            <span>Balanced business and technical acumen</span>
          </ReactTooltip>
          <SkillIcon data-tip data-for="learning" src={Learning} />
          <ReactTooltip id="learning">
            <span>Immersive learning capabilities</span>
          </ReactTooltip>
          <SkillIcon data-tip data-for="insight" src={Insight} />
          <ReactTooltip id="insight">
            <span>Deep analytical insight</span>
          </ReactTooltip>
        </SkillIcons>
      ) : (
        <CodeIcons active={softwareActive}>
          <CodeIcon data-tip data-for="html" src={HTMLIcon} />
          <ReactTooltip id="html">
            <span>HTML</span>
          </ReactTooltip>
          <CodeIcon data-tip data-for="javascript" src={JavascriptIcon} />
          <ReactTooltip id="javascript">
            <span>Javascript</span>
          </ReactTooltip>
          <CodeIcon data-tip data-for="matlab" src={MatlabIcon} />
          <ReactTooltip id="matlab">
            <span>Matlab</span>
          </ReactTooltip>
          <CodeIcon data-tip data-for="python" src={PythonIcon} />
          <ReactTooltip id="python">
            <span>Python</span>
          </ReactTooltip>
          <CodeIcon data-tip data-for="visualbasic" src={VisualBasicIcon} />
          <ReactTooltip id="visualbasic">
            <span>Visual Basic</span>
          </ReactTooltip>
        </CodeIcons>
      )}
    </>
  )
}

export default Skill
